import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { GatsbySeo } from 'gatsby-plugin-next-seo';
import React from 'react';
import Block3Part from '../components/Block3Part';
import BlockCTA from '../components/BlockCTA';
import BlockHeaderWithImage from '../components/BlockHeaderWithImage';

import BlockLogos from '../components/BlockLogos';
import BlockQuote from '../components/BlockQuote';

import Footer from '../components/Footer';
import MoneyRepeatIcon from '../components/icons/MoneyRepeat';
import RocketIcon from '../components/icons/Rocket';
import Layout from '../components/Layout';
import Banner from '../components/Banner';

import { edgesByName } from '../utils/gql';

import IncreaseIcon from '../components/icons/Increase';
import CreditTeamIcon from '../components/icons/CreditTeam';
import PaymentPortalIcon from '../components/icons/PaymentPortal';

import IntegrationIcon from '../components/icons/Integration';
import ResolveDirectAPIIcon from '../components/icons/ResolveDirectAPI';

export const pageQuery = graphql`
  query PartnerPageQuery {
    bg: file(relativePath: { eq: "home/bg.svg" }) {
      publicURL
    }

    images: allFile(filter: { relativeDirectory: { eq: "integrations" } }) {
      edges {
        node {
          name
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid
            }
            fixed(width: 180) {
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    }

    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }

    customerSummaryImage: file(
      relativePath: { eq: "partner/customer-summary.png" }
    ) {
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;

export default function PartnerPage({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);

  const images = edgesByName(data.images, node => node.childImageSharp.fixed);

  return (
    <Layout>
      <GatsbySeo
        title="Learn about Partnerships with Resolve"
        description="Resolve Partnerships will help you drive growth for your clients and your business."
      />
      <Banner />
      <BlockHeaderWithImage
        title={
          <span>
            Become a Resolve <br />
            partner today
          </span>
        }
        desc={
          <span>
            Resolve partnerships means driving growth for yourself and your
            clients. Earn commissions or revenue shares on clients you refer.
            Help your clients unlock growth, enter new markets, and modernize
            their credit and payments workflows with Resolve’s powerful b2b
            payments features.
          </span>
        }
        image={data.customerSummaryImage.childImageSharp.fluid}
        primaryButtonText="Apply now"
        primaryButtonTo="https://b2b.resolvepay.com/partnerships-form"
      />

      <Block3Part
        title="Why partner with Resolve?"
        items={[
          {
            pretitle: <RocketIcon width={64} height={64} />,
            title: 'Grow your client’s business',
            content: (
              <div>
                Resolve allows businesses to take on larger orders and enter new
                markets, without impacting their cash flow. Your clients can win
                more sales, get paid faster, and give their customers more time
                and more ways to pay.
              </div>
            ),
          },
          {
            pretitle: <MoneyRepeatIcon width={64} height={64} />,
            title: 'Implement modern b2b payments',
            content: (
              <div>
                Resolve provides a modern b2b payments experience for your
                client’s customers. The online payments portal gives them both
                more time to pay, and more ways to pay. Resolve completes the
                ideal b2b tech-stack with our integration options.
              </div>
            ),
          },
          {
            pretitle: <IncreaseIcon width={64} height={64} />,
            title: 'Scale your earnings',
            content: (
              <div>
                Recommend the best-in-class solution for your clients, and earn
                commission as part of our revenue share program. We’re invested
                in your growth too, scale your business with Resolve.
              </div>
            ),
          },
        ]}
      />

      <BlockQuote author="SS&amp;Si Dealer Network">
        “Resolve allowed us to expand &amp; enter new markets. We could now take
        larger orders on with net terms, which we previously had to turn down.”
      </BlockQuote>
      <Block3Part
        title="Who should apply?"
        primaryButtonText="Apply now"
        primaryButtonTo="https://b2b.resolvepay.com/partnerships-form"
        items={[
          {
            pretitle: <IntegrationIcon width={64} height={64} />,
            title: 'Financial & e-commerce systems integrators',
            content: (
              <div>
                These partners help their clients with financial processes,
                accounting system connections, and e-commerce payments. Apply to
                see how Resolve integrates with QuickBooks Online &amp; Magento
                2.
              </div>
            ),
          },
          {
            pretitle: <PaymentPortalIcon width={64} height={64} />,
            title: 'B2B marketplaces & e-commerce',
            content: (
              <div>
                Partner with Resolve to solve your b2b marketplace payments.
                Focus on growing your marketplace, we’ll take care of payments.
                Apply now to learn about our API payments experience and see our
                online payment portal demo.
              </div>
            ),
          },
          {
            pretitle: <CreditTeamIcon width={64} height={64} />,
            title: 'Resolve current customers',
            content: (
              <div>
                Our current customers best understand the benefits of Resolve,
                making them the perfect referers . We give our current customers
                the opportunity to earn from their referrals to Resolve. Learn
                about our co-marketing support and options.
              </div>
            ),
          },
        ]}
      />
      <div className="block-background">
        <BlockLogos
          title="We already make b2b payments better for 1,000+ businesses "
          data={[
            {
              image: companies['Linus Bike'],
              name: 'Linus Bike',
              offsetTop: 15,
            },
            {
              image: companies['ConEquip'],
              name: 'ConEquip',
              offsetTop: 10,
            },
            {
              image: companies['Jupiter Pro Parts'],
              name: 'Jupiter Pro Parts',
              offsetTop: -10,
            },
            {
              image: companies['Ozark LED'],
              name: 'Ozark LED',
              offsetTop: 0,
            },
            {
              image: companies['Archipelago Lighting'],
              name: 'Archipelago Lighting',
              offsetTop: 10,
            },
            {
              image: companies['Go Materials'],
              name: 'Go Materials',
              offsetTop: -10,
            },
          ]}
        />

        <Block3Part
          title="Our integrations &amp; connections"
          items={[
            {
              content: (
                <div>
                  <div className="integration-logo quickbooks">
                    <Img fixed={images['quickbooks-logo']} alt="QuickBooks" />
                  </div>

                  <div className="block-2-part__item-content">
                    Our integration reconciles payments and transactions for
                    you. It also automatically imports customer and invoice
                    records. You can choose which invoices to offer net terms on
                    - simply complete the process in our dashboard.
                  </div>
                </div>
              ),
            },
            {
              content: (
                <div>
                  <div className="integration-logo magento2">
                    <Img fixed={images['magento2-logo']} alt="magento2" />
                  </div>

                  <div className="block-2-part__item-content">
                    Resolve's Magento Checkout Extension allows your business
                    customers to apply for net terms at checkout without leaving
                    your online store. Offer the same net terms options across
                    all your sales channels.
                  </div>
                </div>
              ),
            },
            {
              content: (
                <div>
                  <div className="integration-logo api">
                    <ResolveDirectAPIIcon width={250} height={300} />
                  </div>

                  <div className="block-2-part__item-content">
                    Resolve’s API means many different accounting systems and
                    ecomerce platforms can be connected. Schedule a time with
                    our team to learn more.
                  </div>
                </div>
              ),
            },
          ]}
        />
      </div>

      <Block3Part
        title="How to become a partner?"
        primaryButtonPosition="left"
        primaryButtonText="Apply now"
        primaryButtonTo="https://b2b.resolvepay.com/partnerships-form"
        primaryButtonStyle="transparent"
        isPrimaryButton={false}
        items={[
          {
            title: 'Apply now',
            content: (
              <div>
                We encourage everyone who is interested to apply. From
                consultants to co-marketing opportunities. We’ll set up a time
                to chat.
              </div>
            ),
          },
          {
            title: 'Sign',
            content: (
              <div>
                Review our agreement, see all the details on commission and
                revenue-sharing and learn how it works. Sign to become a Resolve
                partner.
              </div>
            ),
          },
          {
            title: 'Start referring ',
            content: (
              <div>
                This can be as simple as 1-to-1 referrals, sharing a bespoke
                link, or creating a co-branded page with us. We’ll make sure you
                get the credit for the referral.
              </div>
            ),
          },
        ]}
      />
      <BlockCTA
        pretitle="PARTNER WITH US TODAY &amp; StART EARNING."
        title="Find out about Resolve Partnerships"
        primaryButtonText="Get Started"
        primaryButtonTo="https://b2b.resolvepay.com/partnerships-form"
      />
      <Footer darkSkin />
      <style jsx>{`
        .block-background {
          background-image: url(${data.bg.publicURL});
          background-repeat: no-repeat;
          background-position: 50%;
          background-size: auto;
        }
        .block-3part {
          margin-top: -300px;
        }
      `}</style>
      <style jsx global>{`
        .integration-logo.api svg {
          margin: -115px 0 -145px -25px;
        }

        .integration-logo.quickbooks picture img,
        .integration-logo.magento2 picture img {
          margin-left: -7px;
        }
      `}</style>
    </Layout>
  );
}
